import * as React from 'react';
import { 
    Button, 
    Stack, 
    Box,
    Container, 
    Paper, 
    Divider, 
    Typography,
    TextField,
    Drawer
} from '@mui/material';
import { Global } from '@emotion/react';
import { ReactComponent as LogoIcon } from './telebook.svg';
import './takeme.css';
import { TakeMeDatePicker } from './TakeMeDatePicker';

function MuiDemo() {

    const [originDialogOpen, setOriginDialogOpen] = React.useState(false);
    const handleClickOpen = () => {
        setOriginDialogOpen(true);
    };
    const handleClose = () => {
        setOriginDialogOpen(false);
    };

    return (
        <Container sx={{
            height: '100%',
            padding: '7px',
            fontStyle: 'normal', }} maxWidth='md'>
            <Box sx={{ height: '100%' }}>
                <Stack 
                    spacing={1} 
                    sx={{ height: '100%' }}
                    direction="column"
                    alignItems="stretch">
                    <Paper sx={{    
                        verticalAlign: 'center',
                        textAlign: 'center',
                        border: 'none',
                        boxShadow: 'none',
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                        justifyContent: 'center',
                        backgroundImage: 'none',
                        backgroundColor: 'transparent',                   
                        transition: 'flex-grow 2s ease',
                        flexGrow: 1 }}>
                        <LogoIcon style={{ margin: '35px' }} />
                        <Typography variant='h1'>Take Me</Typography>
                        <br />
                        <Typography style={{ 
                            width: '80%',
                            fontWeight: 100
                        }}>      </Typography>
                    </Paper>

                    <Paper
                        style={{
                            margin: '10px',
                            backgroundImage: 'none'                            
                        }}>
                        <Stack
                            padding='10px 15px'
                            direction="column"
                            alignItems="stretch"
                            spacing={1}
                            divider={<Divider orientation="horizontal" flexItem />}
                            >
                            <Stack
                                direction="row"
                                justifyContent="space-between"
                                alignItems="center"
                                >
                                <Typography>Travel date</Typography>
                                <TakeMeDatePicker />
                            </Stack>
                            <Stack
                                direction="row"
                                justifyContent="space-between"
                                alignItems="center"
                                >
                                <Typography>Travel date</Typography>
                                <TakeMeDatePicker />
                            </Stack>
                            <Stack
                                direction="row"
                                justifyContent="space-between"
                                alignItems="center"
                                >
                                <Typography>Origin city</Typography>                                
                                <Button
                                    onClick={handleClickOpen}
                                    sx={{
                                        backgroundColor: 'rgba(118, 118, 128, .1)',
                                        padding: '7px 30px',
                                        borderRadius: '10px'
                                    }}
                                ><Typography>Choose city &gt;</Typography>
                                </Button>

                                <Global
                                    styles={{
                                        '.MuiDrawer-root > .MuiPaper-root': {
                                            height: `calc(100%)`,
                                            overflow: 'visible',
                                        }
                                    }}
                                />
                                <Drawer
                                    anchor='bottom'
                                    open={originDialogOpen}
                                    onClose={handleClose}
                                    >
                                    <Stack
                                        sx={{
                                            margin: '20px'
                                        }}>
                                        <TextField 
                                            sx={{
                                                padding: '0px'
                                            }}/>
                                        <Typography>
                                            Enter here the city name and choose from the list
                                        </Typography>
                                    </Stack>
                                </Drawer>

                            </Stack>
                        </Stack>
                    </Paper>

                    <Stack 
                        direction="row"
                        spacing={0.5}
                        sx={{
                            width: '100%',
                            alignItems: 'center',
                            justifyContent: 'center'
                        }}>
                        <Button
                            sx={{ 
                                borderRadius: '30px 4px 4px 30px',
                                padding: '10px 20px 10px 30px'
                            }}
                            variant="contained"></Button>
                        <Button
                            sx={{ 
                                borderRadius: '4px 30px 30px 4px',
                                padding: '10px 30px 10px 20px'
                            }}
                            variant="contained"></Button>
                    </Stack>
                </Stack>
            </Box>
        </Container>
    );
}

export default MuiDemo;